import React, { Component } from 'react'
import {
  Backdrop,
  Fade,
  Modal,
  Container,
  Icon,
  FormControl,
  InputLabel,
  Input,
  FormHelperText,
  TextField,
  MenuItem,
  Button,
} from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import GradientStepper from '@Components/Stepper/GradientStepper'
import styles from './styles'

const STEPS = [
  {
    label: 'ข้อมูล นศ.',
    icon: (
      <Icon
        className="fas fa-user-graduate"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
  {
    label: 'ข้อมูลส่วนตัว',
    icon: (
      <Icon
        className="fas fa-user"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
  {
    label: 'ข้อมูลการติดต่อ',
    icon: (
      <Icon
        className="fas fa-phone-volume"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
]

const MAJOR = [
  { label: 'วิศวะกรรมซอฟต์แวร์', value: 'SE' },
  { label: 'การจัดการสมัยใหม่และเทคโนโลยีสารสนเทศ', value: 'MMIT' },
  { label: 'แอนิเมชั่นและวิชวลเอฟเฟกต์', value: 'ANI' },
  { label: 'ดิจิทัลเกม', value: 'DG' },
  { label: 'บูรณาการอุตสาหกรรมดิจิทัล', value: 'DII' },
]

const defaultForm = {
  stdID: '',
  major: '',
}

export class RegisterContainer extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    visible: PropTypes.bool,
    handleClose: PropTypes.func,
  }

  static defaultProps = {
    visible: false,
    handleClose: () => {},
  }

  state = {
    formValues: defaultForm,
    errors: defaultForm,
  }

  handleChange = name => event => {
    const { setValue } = this
    const { value } = event.target
    event.persist()
    if (name === 'stdID') {
      const regex = /[^0-9]/g
      const filtered = value.replace(regex, '')
      setValue(name, filtered.substring(0, 9))
    }
  }

  setValue = (name, value) =>
    this.setState(state => ({
      formValues: {
        ...state.formValues,
        [name]: value,
      },
      errors: {
        ...state.errors,
        [name]: '',
      },
    }))

  handleSubmitStep1 = () => {
    const stdIDValidate = /[0-9]{9}/g
    const { formValues } = this.state
    const newErrors = defaultForm
    if (!formValues.stdID) {
      newErrors.stdID = 'กรุณากรอกรหัสนักษึกษา'
    } else if (!formValues.stdID.match(stdIDValidate)) {
      newErrors.stdID = 'รหัสนักศึกษาต้องมี9ตัวอักษร'
    }

    if (!formValues.major) {
      newErrors.major = 'กรุณาเลือกสาขา'
    }

    this.setState({ errors: newErrors })
  }

  handleSubmit = event => {
    event.preventDefault()
  }

  render() {
    const { visible, classes, handleClose } = this.props
    const { formValues, errors } = this.state

    return (
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={visible}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={visible}>
          <Container className={classes.paper}>
            <GradientStepper activeStep={0} steps={STEPS} />
            <form className={classes.formStyle}>
              <FormControl className={classes.inputStyle} error={!!errors.stdID}>
                <InputLabel htmlFor="stdID">รหัส นักศึกษา</InputLabel>
                <Input
                  id="stdID"
                  value={formValues.stdID}
                  onChange={this.handleChange('stdID')}
                  aria-describedby="stdID-text"
                />
                {errors.stdID && <FormHelperText id="stdID-text">{errors.stdID}</FormHelperText>}
              </FormControl>
              <TextField
                id="major"
                select
                label="สาขา"
                className={classes.inputStyle}
                value={formValues.major}
                onChange={this.handleChange('major')}
                error={!!errors.major}
                helperText={errors.major || ' '}
              >
                {MAJOR.map(option => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>
              <Button variant="contained" onClick={this.handleSubmitStep1}>
                testttt
              </Button>
            </form>
          </Container>
        </Fade>
      </Modal>
    )
  }
}

export default withStyles(styles)(RegisterContainer)
